import React, { Component } from 'react';
import api from './Resources/sensitive/api.json';
import { IconButton } from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import GoogleMapReact from 'google-map-react';
import './Map.css';

const Circle = ({ text, size, current, bind }) => {
  return (
    current ? <div
      className='circle currentlocation'
      style={{ width: size + 'px', height: size + 'px' }}
    >
      {size < 50 ? '' : text}
    </div>
      : <div
        className='circle'
        style={{ width: size + 'px', height: size + 'px' }}
        onClick={bind}>
        {size < 50 ? '' : text}
      </div>)
};


const InfoWindow = ({ venue, date, bind }) =>
  <Card>
    <CardContent className='info'>
      <div className='align-row space-between'>
        <Typography gutterBottom variant='h6' component='h6'>
          {venue.name}
        </Typography>

        {
          venue.address ? <IconButton aria-label="close" onClick={bind}>
            <CloseIcon fontSize="small" />
          </IconButton>
            : null
        }
      </div>
      {venue.address ?
        <Typography variant='body2' color='textSecondary' component='p'>
          Address: {venue.address}
        </Typography>
        : null}
      <div className='align-row space-around'>
        {venue.current_popularity ?
          <div className='detail align-column'>
            <Typography variant='body2' color='textSecondary' component='p'>
              Real time
            </Typography>
            <Typography variant='body2' color='textSecondary' component='p'>
              {venue.current_popularity} %
            </Typography>
          </div>
          : null
        }
        {venue.populartimes ?
          <div className='detail align-column'>
            <Typography variant='body2' color='textSecondary' component='p'>
              Typical level
          </Typography>
            <Typography variant='body2' color='textSecondary' component='p'>
              {venue.populartimes[date[0]].data[date[1]]} %
          </Typography>
          </div>
          : null
        }
      </div>
    </CardContent>
  </Card>;



export default class MapContainer extends Component {
  constructor(props) {
    super(props)
    this.state = {
      venueData: [],
      venueDetail: {
        name: 'Click on a Venue for detail',
      },
      date: this.props.currentime,
    }
  }

  componentDidMount() {
  }

  clickVenue(venue) {
    this.setState({
      venueDetail: venue
    });
    this.props.clickVenue(venue);
  }

  closeMenu() {
    this.setState({
      venueDetail: {
        name: 'Click on a Venue for detail',
      },
    })
    this.props.closeVenue();
  }

  render() {

    return (

      <div className="map-container">
        <div style={{ height: '67vh', width: '100%' }}>
          <GoogleMapReact
            bootstrapURLKeys={{ key: api.name }}
            center={{ lat: this.props.centre[0], lng: this.props.centre[1] }}
            defaultZoom={15}
          >
            {this.props.venue.map(venue => {
              return (
                <Circle
                  key={venue.id}
                  lat={venue.coordinates.lat}
                  lng={venue.coordinates.lng}
                  text={venue.name}
                  size={venue.populartimes[this.state.date[0]].data[this.state.date[1]]}
                  bind={this.clickVenue.bind(this, venue)}
                />
              )
            })}
            {this.props.centre[2] ?
              <Circle
                lat={this.props.centre[0]}
                lng={this.props.centre[1]}
                size={20}
                current={true}
              /> :
              null}
          </GoogleMapReact>
          <InfoWindow
            venue={this.state.venueDetail}
            date={this.state.date}
            bind={this.closeMenu.bind(this)}
          />
        </div>
      </div>
    )
  }
}

