import React from "react";
import MapComponent from './Map.js';
import { getData } from '../components/APIClient';
import HeatMap, { Style } from "jsheatmap";
import './Home.css';



// Heat map ----
const Headings = ({ data }) => {
  const titles = () => data.map(h => <th key={h}>{h}</th>)
  return (<tr><th></th>{titles()}</tr>);
}
const background = (rgb) => {
  return `rgb(${rgb.red * 100}%, ${rgb.green * 100}%, ${rgb.blue * 100}%)`;
}

const cols = cells => {
  return cells.colors.map((c, i) => <td key={i}>
    <div style={{ background: background(c), width: '3vw' }}>
      <span style={{ fontSize: '0.3em' }}>{cells.scales[i].toFixed(1)}</span>
    </div>
  </td>);
}

const Rows = ({ data }) =>
  data.rows.map((row, i) => <tr key={row.label}>
    <td>{row.label}</td>{cols(row.cells)}
  </tr>
  );

const PopularHeatMap = ({ populartimes }) => {
  const hour = ['1a', '2a', '3a', '4a', '5a', '6a', '7a', '8a', '9a', '10a', '11a', '12a', '1p', '2p', '3p', '4p', '5p', '6p', '7p', '8p', '9p', '10p', '11p', '12p'];
  const days = populartimes.map((day) => [day.name, [...day.data]]);
  const heatMap = new HeatMap(hour, days);
  console.log(heatMap);
  const data = heatMap.getData({ style: Style.SIMPLE });
  return (
    <table className="heat-map">
      <tbody>
        <Headings data={data.headings} />
        <Rows data={data} />
      </tbody>
    </table>
  )
}

const PopularHeatMapVertical = ({ populartimes }) => {
  const hour = ['1a', '2a', '3a', '4a', '5a', '6a', '7a', '8a', '9a', '10a', '11a', '12a', '1p', '2p', '3p', '4p', '5p', '6p', '7p', '8p', '9p', '10p', '11p', '12p'];
  const days = populartimes.map((day, i) => day.name);
  const time = populartimes.map((day) => [day.data]);
  const vertime = time[0].map((_) => time.map((row, i) => [hour[i], ...row]));
  const heatMap = new HeatMap(days, vertime);
  const data = heatMap.getData({ style: Style.SIMPLE });
  return (
    <table className="heat-map">
      <tbody>
        <Headings data={data.headings} />
        <Rows data={data} />
      </tbody>
    </table>
  )
}

// Info Detail Window:
const InfoWindow = ({ venue, date }) => {
  return (
    <div className="heat-map-container">
      <PopularHeatMap className='heat-map' populartimes={venue.populartimes} />
      {/* <PopularHeatMapVertical className='heat-map-vertical' populartimes={venue.populartimes} /> */}
    </div>
  )
};


export class Home extends React.Component {
  state = {
    showDetail: false,
    venueDetail: {
      populartimes: [{
        name: '',
        data: []
      }],
    }
  };

  constructor(props) {
    super(props);
    this.state = {
      showingInfoWindow: false,
      currentLocation: this.props.currentLocation,
      currentime: [0, 0, 0],
      activeMarker: {},
      selectedPlace: {},
      venueData: [],
      events: []
    };
  }
  componentDidMount() {
    getData('Shops').then((data) => {
      this.setState({
        venueData: data
      }, () => {
        this.check_for_date();
        this.forceUpdate();
      })
    })
  }

  check_for_date() {
    const current_date = new Date();
    const convday = current_date.getDay() - 1 < 0
      ? 6 : current_date.getDay() - 1;
    this.setState({
      currentime:
        [convday, current_date.getHours(), current_date.toLocaleString('en-US', { hour: 'numeric', hour12: true })]
    });
  }

  clickVenue(venue) {
    console.log(venue);
    this.setState({
      showDetail: true,
      venueDetail: venue
    })
  }

  closeVenue() {
    this.setState({
      showDetail: false,
    })
    this.forceUpdate();
  }


  render() {

    return (
      <div className='Home'>

        <div
          style={{
            'backgroundColor': 'white',
            'width': '100vw',
            'position': 'relative',
            'borderRadius': '10px',
            'boxShadow': '0 4px 4px 0 rgba(0,0,0,0.1)'
          }}
        >
          <MapComponent
            google={this.props.google}
            centre={this.props.currentLocation}
            onClick={this.onMapClicked}
            venue={this.state.venueData}
            zoom={10}
            currentime={this.state.currentime}
            clickVenue={this.clickVenue.bind(this)}
            closeVenue={this.closeVenue.bind(this)}
          />
        </div>

        <div className='Searchsection'
          style={{
            'backgroundColor': 'white',
            'marginBottom': '20px',
            'padding': '20px',
            'borderRadius': '10px',
            'boxShadow': '0 4px 4px 0 rgba(0,0,0,0.1)'
          }}>
          {this.state.showDetail
            ? <InfoWindow
              venue={this.state.venueDetail}
            />
            : null
          }
        </div>
      </div>
    )
  }
}

export default Home
